import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { FormGroup, FormBuilder, Validators,FormControl,FormArray } from '@angular/forms';
import { CommonService } from '../common.service';

@Component({
  selector: 'app-inventory',
  templateUrl: './inventory.component.html',
  styleUrls: ['./inventory.component.css']
})
export class InventoryComponent implements OnInit {
  model_list: any[] = [];
  constructor(private commonservice: CommonService,private http: HttpClient) { }

  ngOnInit() {
    this.get_model_data();
  }

  get_model_data()
  {
    return this.http.get("http://127.0.0.1:8000/index.php/api/get_model_data").subscribe(response => {
      console.log(response)
      if(response['success'] == true)
      {
        this.model_list = response['data'];
        this.model_list.map(v=>{
          v['created_at'] = new Date();
          v['images'] = JSON.parse(v['images']);
          return v;
        })
        // console.log("modelList",this.model_list);
        // this.errorMessage = "";
        // this.successMessage = "Data is successfully Stored";
        // this.success_show = true;
       // this.router.navigate(['/orderdetails',response['DATA']['responce_id']]);
      }else{
        // this.errorMessage = response['info'];
        // this.error_show = true;
        // this.successMessage = "";
      }
     
    });
  }

}
