import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { FormGroup, FormBuilder, Validators,FormControl,FormArray } from '@angular/forms';
import { CommonService } from '../../common.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css']
})
export class AddComponent implements OnInit {
  successMessage: string = "";
  errorMessage: string = "";
  error_show: boolean = false;
  success_show: boolean = false;

  manufacturer_list: [] = [];

  add_manufacturur = new FormGroup({
    manufacturer_name: new FormControl('')
  });
  response;
  constructor(private commonservice: CommonService,private http: HttpClient) { }

  ngOnInit() {
    this.get_manufacturer_data();
  }

  get_manufacturer_data()
  {
    return this.http.get("http://127.0.0.1:8000/index.php/api/get_manufacturer_data").subscribe(response => {
      console.log(response)
      if(response['success'] == true)
      {
        this.manufacturer_list = response['data'];
        
        // this.errorMessage = "";
        // this.successMessage = "Data is successfully Stored";
        // this.success_show = true;
       // this.router.navigate(['/orderdetails',response['DATA']['responce_id']]);
      }else{
        this.errorMessage = response['info'];
        this.error_show = true;
        // this.successMessage = "";
      }
     
    });
  }

  onSubmit()
  {

    //this.response = this.commonservice.get_data_by_post(this.add_manufacturur.value);

    return this.http.post("http://127.0.0.1:8000/index.php/api/store_manufacturer_data",this.add_manufacturur.value).subscribe(response => {
      console.log(response)
      if(response['success'] == true)
      {
        // this.errorMessage = "";
        this.successMessage = "Data is successfully Stored";
        this.success_show = true;
        this.error_show = false;
        this.get_manufacturer_data();
        this.add_manufacturur.reset();
       // this.router.navigate(['/orderdetails',response['DATA']['responce_id']]);
      }else{
        this.errorMessage = response['info'];
        this.error_show = true;
        this.success_show = false;
        // this.successMessage = "";
      }
     
    });

    console.log(this.response);
    // console.log(this.add_manufacturur.value);
  }

}
